.experience {
	padding: 69px 0;
	@include md {
		padding: 96px 0 84px; }
	@include sm {
		padding: 37px 0 40px; }
	&__inner {
		max-width: 1000px;
		@include md {
			padding: 0 15px; } }
	&__title {
		margin-bottom: 20px;
		@include md {
			margin-bottom: 21px; } }
	&__wrap {
		margin-top: 149px;
		padding: 0 15px;
		@include md {
			margin-top: 49px;
			padding: 0 23px; }
		@include md {
			padding: 0 14px; } }
	&__row {
		display: table;
		table-layout: fixed;
		width: 100%;
		@include clr;
		@include md {
			display: block; }
		&_mod {
			@include md {
				display: flex;
				flex-direction: column; } } }
	&__left,
	&__right {
		vertical-align: middle;
		display: table-cell;
		@include clr;
		@include md {
			padding: 0;
			width: 100%;
			text-align: center;
			display: block; }
		.experience__row_mod & {
			@include md {
				padding: 0;
				width: 100%; } } }
	&__left {
		padding-right: 22px;
		width: 64.3%;
		.experience__row_mod & {
			padding-right: 20px;
			width: 35.7%;
			@include md {
				order: 2; } } }
	&__right {
		padding-left: 25px;
		width: 35.7%;
		.experience__row_mod & {
			width: 64.3%;
			@include md {
				order: 1; } } }
	&__img {
		margin: -52px 0 0;
		@include clr;
		@include md {
			margin: 0; }
		img {
			float: right;
			border: 2px solid $blue;
			display: block;
			@include md {
				float: none;
				width: 100%; }
			.experience__row_mod & {
				float: left;
				border: 2px solid $green;
				@include md {
					float: none; } } } }
	&__info {
		margin-top: 30px;
		padding-right: 22px;
		padding-left: 15px;
		width: 64.3%;
		@include f(14, 20);
		color: rgba($white, 0.5);
		@include clr;
		@include md {
			margin-top: 0;
			padding: 0;
			width: 100%; } }
	&__info-text {
		float: right;
		max-width: 770px;
		@include md {
			float: none;
			margin: 0 auto;
			max-width: 597px;
			text-align: center; } }
	&__text {
		position: relative;
		min-height: 303px;
		width: 100%;
		max-width: 360px;
		margin-top: -45px;
		padding: 27px 0 2px;
		display: table;
		@include md {
			margin: 0 auto;
			padding: 30px 0 50px;
			max-width: 597px;
			min-height: auto;
			display: block; }
		@include sm {
			padding: 19px 0 28px; }
		.experience__row:first-child & {
			margin-top: -178px;
			padding: 10px 0;
			@include md {
				margin: 0 auto;
				padding: 30px 0 50px; }
			@include sm {
				padding: 19px 0 28px; } }
		.experience__row:last-child & {
			margin-top: -2px;
			padding: 10px 0;
			@include md {
				margin: 0 auto;
				padding: 30px 0 43px; }
			@include sm {
				padding: 19px 0 28px; } }
		.experience__row_mod & {
			float: right;
			@include md {
				float: none; } }
		&:after,
		&:before {
			position: absolute;
			content: "";
			background-color: $lightGray;
			@include md {
				display: none; }
			.experience__row_mod & {
				left: auto; } }
		&:before {
			top: 0;
			left: -20px;
			bottom: 0;
			width: 1px;
			.experience__row_mod & {
				right: -20px; } }
		&:after {
			top: 50%;
			left: -95px;
			width: 75px;
			height: 1px;
			.experience__row_mod & {
				left: auto;
				width: 50px;
				right: -69px; } } }
	&__text-wrap {
		width: 100%;
		font-size: 16px;
		text-align: left;
		vertical-align: middle;
		display: table-cell;
		.experience__row_mod & {
			text-align: right;
			@include md {
				text-align: center; } }
		@include md {
			text-align: center;
			display: block; }
		@include sm {
			@include f(14,22); } }
	&__title-md {
		margin-bottom: 19px;
		@include f(18, 22, null, 700);
		@include md {
			margin-bottom: 18px; }
		@include sm {
			margin-bottom: 12px;
			font-size: 16px; } }
	&__footer {
		margin-top: 17px;
		@include md {
			display: none; } }
	&__link {
		color: $blue;
		@include f(12, 24, null, 700);
		letter-spacing: .158em;
		text-transform: uppercase;
		transition: color .3s ease-in-out;
		@include hover {
			color: lighten($blue, 20); } } }
