.sm-social {
	font-size: 0;
	&__label,
	&__list,
	&__item,
	&__link {
		display: inline-block;
		vertical-align: middle; }
	&__label {
		padding-left: 40px;
		color: #F3F3F8;
		@include f(14, 17);
		@include sm {
			padding: 0 15px; } }
	&__link {
		padding: 0 40px 0 20px;
		@include sm {
			padding: 0 15px; }
		@include hover {
			.icon {
				opacity: 1; } }
		.icon {
			color: $white;
			opacity: 0.5;
			font-size: 36px;
			transition: opacity 0.175s ease-in-out;
			@include sm {
				font-size: 28px; }
			&-twitter {
				font-size: 30px;
				@include sm {
					font-size: 24px; } } } } }
