.modal {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: auto;
	padding: 15px;
	opacity: 0;
	visibility: hidden;
	background: rgba($black, 0.3);
	@include vertical;
	transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
	@include xs {
		padding: 0; }
	&.is-open {
		opacity: 1;
		visibility: visible; }
	&__container {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		border-radius: 30px;
		width: 100%;
		max-width: 970px;
		color: $black;
		background: $white;
		@include f(16, 24);
		@include sm {
			@include f(14, 22); }
		@include xs {
			border-radius: 0; } } }
