.profiles {
	padding: 105px 0 215px;
	@include md {
		padding: 105px 0 202px; }
	@include sm {
		padding: 61px 0 51px; }
	&__out {
		&:not(:first-child) {
			margin-top: 87px;
			@include md {
				margin-top: 49px; }
			@include sm {
				margin-top: 50px; } } }
	&__title {
		margin-bottom: 22px;
		@include md {
			margin-bottom: 22px; }
		@include sm {
			margin-bottom: 25px; } }
	&__title-md {
		@include f(16,19,100,700);
		text-transform: uppercase;
		.profiles__row_mod & {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			@include sm {
				position: relative; } } }
	&__row {
		color: $blue;
		text-align: center;
		font-size: 0;
		&:not(:last-child) {
			margin-bottom: 6px;
			@include md {
				margin-bottom: 9px; }
			@include sm {
				margin-bottom: 49px; } }
		&_mod {
			color: $green; } }
	&__wrap {
		position: relative;
		&:not(:first-child) {
			margin-top: 36px;
			@include sm {
				margin-top: 26px; } }
		.profiles__row_mod & {
			margin-top: 0;
			padding-bottom: 62px;
			@include md {
				padding-bottom: 58px; }
			@include sm {
				padding-bottom: 0; } } }

	&__item {
		position: relative;
		width: 150px;
		height: 168px;
		line-height: 168px;
		font-size: 18px;
		vertical-align: top;
		display: inline-block;
		@include sm {
			width: auto;
			height: auto;
			line-height: initial;
			display: block; }
		&:not(:last-child) {
			margin-right: 50px;
			@include md {
				margin-right: 34px; }
			@include sm {
				margin: 0 0 13px; } } }
	&__svg {
		position: absolute;
		top: 0;
		left: 0;
		@include sm {
			display: none; }
		svg,
		svg path {
			stroke: currentColor; } }
	&__name {
		position: relative;
		color: $white;
		max-width: 130px;
		line-height: 1.5;
		vertical-align: middle;
		display: inline-block;
		@include sm {
			max-width: 100%;
			display: block; } } }
