.diagram {
	position: relative;
	margin: 0 auto 80px;
	width: 1220px;
	height: 620px;
	@include xl {
		margin-bottom: 50px;
		width: 720px;
		height: 1130px; }
	@include sm {
		padding-top: 1160px;
		margin-bottom: 0;
		max-width: 290px;
		height: auto; }
	&__in,
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; }
	&__overlay {
		z-index: 5;
		opacity: 0;
		visibility: hidden;
		background: rgba($black, 0.5);
		transition: opacity 0.2s ease, visibility 0.2s;
		&.is-open {
			opacity: 1;
			visibility: visible; } }
	//bg
	&__bg {
		position: absolute;
		top: 60px;
		left: 50px;
		right: 50px;
		bottom: 0;
		@include xl {
			top: 0;
			left: 0;
			right: 0; }
		@include sm {
			display: none; }
		&-header,
		&-body {
			position: absolute;
			top: 0;
			bottom: 0;
			border-radius: 3px;
			background: rgba($white, .1);
			@include xl {
				left: 0;
				right: 0; } }
		&-header {
			left: 0;
			width: 124px;
			@include xl {
				top: 40px;
				bottom: auto;
				width: auto;
				height: 100px; } }
		&-body {
			left: 248px;
			right: 0;
			@include xl {
				top: 266px;
				left: 0;
				bottom: 41px; } }
		&-fork {
			position: absolute;
			top: 10px;
			left: 443px;
			bottom: 10px;
			font-size: 0;
			@include xl {
				top: auto;
				left: 20px;
				right: 20px;
				bottom: 193px; }
			div {
				display: inline-block;
				vertical-align: top;
				margin: 0 1px;
				border-radius: 3px;
				width: 124px;
				height: 100%;
				background: #232629;
				@include xl {
					margin: 0 0 2px;
					width: 100%;
					height: 90px; } } } }
	//lines
	&__lines {
		position: absolute;
		top: 60px;
		left: 50px;
		right: 50px;
		bottom: 0;
		&-fork,
		&-hint {
			background-repeat: no-repeat;
			background-position: left center;
			background-size: contain; }
		&-fork {
			position: absolute;
			&[data-diagram="fork-1"] {
				top: 38px;
				left: 144px;
				width: 358px;
				height: 158px;
				background-image: url(../img/diagram-fork-1.svg);
				@include xl {
					top: 100px;
					left: 195px;
					transform: rotate(90deg);
					transform-origin: left top; } }
			&[data-diagram="fork-2"] {
				top: 67px;
				left: 545px;
				width: 87px;
				height: 103px;
				background-image: url(../img/diagram-fork-2.svg);
				@include xl {
					top: 500px;
					left: 166px;
					transform: rotate(90deg);
					transform-origin: left top; } }
			&[data-diagram="fork-3"] {
				top: 66px;
				right: 127px;
				width: 363px;
				height: 270px;
				background-image: url(../img/diagram-fork-3.svg);
				@include xl {
					top: 545px;
					left: -40px;
					width: 226px;
					height: 477px;
					background-image: url(../img/diagram-fork-3-tablet.svg); } } }
		&-hint {
			position: absolute;
			top: -43px;
			left: 284px;
			width: 46px;
			height: 225px;
			background-image: url(../img/diagram-hint-1.svg);
			@include xl {
				top: 239px;
				left: -25px;
				transform: rotate(-90deg) scaleX(-1);
				transform-origin: left top; } } }
	//info
	&__info {
		position: absolute;
		left: 50px;
		right: 50px;
		bottom: 0;
		height: 203px;
		@include xl {
			top: 0;
			left: auto;
			right: 0;
			bottom: 0;
			width: 373px;
			height: auto; }
		@include sm {
			position: relative;
			width: 100%; }
		&-box {
			position: absolute;
			top: 0;
			padding: 0 10px;
			width: 124px;
			@include f(14, 20);
			@include xl {
				padding-left: 50px;
				width: 100%;
				@include f(16, 22); }
			@include sm {
				position: static;
				padding: 0 0 30px;
				text-align: center; }
			&[data-diagram="info-1"] {
				left: 0;
				@include xl {
					top: 64px; } }
			&[data-diagram="info-2"] {
				left: 124px;
				@include xl {
					top: 164px; } }
			&[data-diagram="info-3"] {
				left: 248px;
				width: 250px;
				@include xl {
					top: 290px;
					width: 100%; } }
			&[data-diagram="info-4"] {
				left: 692px;
				@include xl {
					top: 732px; } }
			&[data-diagram="info-5"] {
				left: 816px;
				@include xl {
					top: 824px; } }
			&[data-diagram="info-1"],
			&[data-diagram="info-2"],
			&[data-diagram="info-3"],
			&[data-diagram="info-4"],
			&[data-diagram="info-5"] {
				@include xl {
					left: auto;
					right: 0; } } }
		&-icon {
			margin-bottom: 15px;
			font-size: 0;
			@include xl {
				position: absolute;
				top: 0;
				left: 0; }
			@include sm {
				position: relative; }
			img {
				width: 30px; } }
		&-title {
			margin-bottom: 20px;
			font-weight: 700;
			@include xl {
				margin-bottom: 10px;
				@include f(18, 20); } } }
	//header
	&__header {
		position: absolute;
		top: 77px;
		left: 0;
		@include clr;
		@include xl {
			top: 0;
			left: 67px; }
		&-col {
			position: relative;
			padding: 21px 30px 18px 72px;
			margin-bottom: 19px;
			width: 170px;
			height: 90px;
			@include xl {
				float: left;
				padding: 49px 5px 20px;
				margin: 0 19px 0 0;
				width: 90px;
				height: 136px;
				text-align: center; }
			&[data-diagram-modal] {
				&.is-active {
					color: $black;
					&:after {
						opacity: 1; }
					&:before {
						opacity: 0; } } }
			&:before,
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: contain;
				transition: opacity 0.2s ease-in-out; }
			&:before {
				background-image: url(../img/diagram-header.svg);
				@include xl {
					background-image: url(../img/diagram-header-tablet.svg); } }
			&:after {
				opacity: 0;
				background-image: url(../img/diagram-header-white.svg);
				@include xl {
					background-image: url(../img/diagram-header-white-tablet.svg); } } }
		&-label {
			position: absolute;
			z-index: 1;
			top: 0;
			left: 0;
			bottom: 0;
			width: 50px;
			font-size: 16px;
			line-height: 50px;
			text-align: center;
			writing-mode: vertical-lr;
			transform: scale(-1);
			@include xl {
				width: 100%;
				line-height: 40px;
				writing-mode: initial;
				@include gpu; } }
		&-value {
			position: relative;
			z-index: 1;
			@include f(14, 20); } }
	//body
	&__body {
		position: absolute;
		top: 60px;
		left: 174px;
		right: 0;
		bottom: 0;
		@include xl {
			top: 140px;
			left: 0;
			right: 0;
			bottom: 0; } }
	&__box {
		position: absolute;
		border-radius: 3px;
		background: #30363D;
		@include vertical;

		&[data-diagram="box-1"],
		&[data-diagram="box-2"] {
			top: 17px; }
		&[data-diagram="box-1"] {
			left: 10px;
			@include xl {
				top: 11px;
				left: 67px; } }
		&[data-diagram="box-2"] {
			left: 330px;
			@include xl {
				top: 330px;
				left: 67px; } }
		&[data-diagram="fork-box-1"] {
			top: 0;
			@include xl {
				left: 0; } }
		&[data-diagram="fork-box-2"] {
			top: 110px;
			@include xl {
				left: 110px; } }
		&[data-diagram="fork-box-1"],
		&[data-diagram="fork-box-2"] {
			left: 80px;
			width: 84px;
			height: 90px;
			@include xl {
				top: 80px; } }
		&[data-diagram="fork-sm-box-1"],
		&[data-diagram="fork-sm-box-3"] {
			top: 15px;
			@include xl {
				left: auto;
				right: 0; } }
		&[data-diagram="fork-sm-box-2"],
		&[data-diagram="fork-sm-box-4"] {
			top: 117px;
			@include xl {
				left: 0; } }
		&[data-diagram="fork-sm-box-1"],
		&[data-diagram="fork-sm-box-2"] {
			left: 10px;
			@include xl {
				top: 5px; } }
		&[data-diagram="fork-sm-box-3"],
		&[data-diagram="fork-sm-box-4"] {
			left: 136px;
			@include xl {
				top: 97px; } }
		&[data-diagram="fork-sm-box-1"],
		&[data-diagram="fork-sm-box-2"],
		&[data-diagram="fork-sm-box-3"],
		&[data-diagram="fork-sm-box-4"] {
			padding: 0 10px;
			width: 104px;
			height: 70px;
			strong {
				display: block; } }
		&_lg {
			width: 104px;
			height: 200px;
			@include xl {
				width: 200px;
				height: 104px; } }
		&-target {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			@include vertical; }
		&-in {
			display: inline-block;
			vertical-align: middle;
			width: 100%;
			@include f(14, 20); } }
	&__fork {
		position: absolute;
		&[data-diagram="fork-1"] {
			top: 17px;
			left: 126px;
			@include xl {
				top: 126px;
				left: 67px;
				width: 200px; } }
		&[data-diagram="fork-2"] {
			top: 46px;
			left: 454px;
			@include xl {
				top: 485px;
				left: 45px;
				width: 241px; } }
		&[data-diagram="fork-3"] {
			top: 17px;
			left: 569px;
			@include xl {
				top: 577px;
				left: 45px;
				width: 241px; } }
		&-hint {
			position: absolute;
			top: -77px;
			left: 80px;
			border: 1px solid $white;
			border-radius: 20px;
			width: 110px;
			line-height: 38px;
			text-align: center;
			@include xl {
				top: 79px;
				left: -59px;
				width: 40px;
				height: 110px;
				writing-mode: vertical-lr; } }
		&-label {
			position: absolute;
			left: 20px;
			border-radius: 50%;
			width: 29px;
			height: 29px;
			color: $black;
			background: $white;
			font-size: 20px;
			line-height: 29px;
			text-align: center;
			&[data-diagram="percent-1"] {
				top: 11px;
				@include xl {
					left: 7px; } }
			&[data-diagram="percent-2"] {
				top: 163px;
				@include xl {
					left: auto;
					right: 10px; } }
			&[data-diagram="percent-1"],
			&[data-diagram="percent-2"] {
				@include xl {
					top: 19px; } } }
		&-title {
			position: absolute;
			top: 48px;
			left: 0;
			width: 37px;
			height: 104px;
			font-size: 14px;
			line-height: 37px;
			text-align: center;
			writing-mode: vertical-lr;
			transform: scale(-1);
			@include xl {
				top: 0;
				left: 0;
				right: 0;
				width: auto;
				height: 37px;
				writing-mode: initial;
				@include gpu; } }
		&-color {
			position: relative;
			margin-bottom: 61px;
			border-radius: 3px;
			width: 104px;
			height: 40px;
			font-size: 14px;
			font-weight: 700;
			line-height: 40px;
			text-align: center;
			@include xl {
				position: absolute;
				top: 0; }
			&_blue {
				background: $blue;
				@include xl {
					right: 0; } }
			&_green {
				background: $green;
				@include xl {
					left: 0; } } } }
	&__hints {
		position: absolute;
		right: 0;
		border-radius: 3px;
		width: 204px;
		background: rgba(#30363D, 0.5);
		@include xl {
			padding: 11px 10px 62px;
			width: 120px; }
		&[data-diagram="hints-1"] {
			top: 28px;
			height: 220px;
			@include xl {
				top: 768px;
				left: 35px;
				right: auto;
				height: 222px; }
			&:before {
				content: '';
				position: absolute;
				top: 84px;
				left: 0;
				right: 50px;
				border-top: 4px solid #1a1a1a;
				@include xl {
					display: none; } } }
		&[data-diagram="hints-2"] {
			top: 278px;
			height: 115px;
			@include xl {
				top: 865px;
				left: 171px;
				height: 125px; } }
		&-label {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 52px;
			font-size: 16px;
			font-weight: 700;
			line-height: 52px;
			text-align: center;
			writing-mode: vertical-lr;
			@include xl {
				writing-mode: initial;
				top: auto;
				left: 0;
				bottom: 0;
				width: auto;
				line-height: 41px; } }
		&-item {
			position: absolute;
			left: 20px;
			padding: 7px 0 8px;
			border-radius: 3px;
			width: 103px;
			color: $green;
			background: #30363D;
			font-size: 14px;
			line-height: 15px;
			text-align: center;
			&_blue {
				color: $blue; }
			&[data-diagram="hint-item-1"] {
				top: 24px; }
			&[data-diagram="hint-item-2"] {
				top: 126px; }
			&[data-diagram="hint-item-3"] {
				top: 173px; }
			&[data-diagram="hint-item-4"] {
				top: 31px;
				padding: 10px 0; }
			&[data-diagram="hint-item-1"],
			&[data-diagram="hint-item-2"],
			&[data-diagram="hint-item-3"],
			&[data-diagram="hint-item-4"] {
				@include xl {
					position: relative;
					top: auto;
					left: auto;
					margin-bottom: 10px; } }
			small {
				display: block;
				font-size: 11px; } } }
	&__logo {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		padding: 16px 0 0;
		font-size: 0;
		text-align: center;
		@include xl {
			padding-top: 26px; }
		@include sm {
			position: relative;
			top: auto;
			padding-top: 6px;
			padding-bottom: 26px; }
		&:after {
			content: '';
			display: block;
			margin: 0 auto;
			border-top: 3px solid #1A1A1A;
			max-width: 1120px; }
		img {
			margin-bottom: 16px;
			width: 143px;
			@include xl {
				margin-bottom: 19px; }
			@include sm {
				margin-bottom: 16px; } } }
	[data-diagram-modal="parent"] {
		transition: color 0.2s ease, background 0.2s;
		&.is-active {
			z-index: 10;
			&:not([data-diagram-hover="no"]) {
				color: $black;
				background: $white; }
			[data-diagram-modal="child"] {
				opacity: 1;
				visibility: visible; } } }
	&__modal {
		position: absolute;
		z-index: 10;
		top: 100%;
		left: 100%;
		padding: 15px 20px 18px;
		//margin: -21px 0 0 -64px
		border-radius: 10px;
		width: 400px;
		opacity: 0;
		visibility: hidden;
		color: $white;
		background: #30363D;
		@include f(13, 19);
		text-align: left;
		pointer-events: none;
		transition: opacity 0.2s ease, visibility 0.2s;
		@include xl {
			display: none; }
		p {
			margin-bottom: 5px; }
		ul {
			list-style-type: disc;
			list-style-position: inside; } } }
