.member {
	padding: 20px 15px;
	text-align: center;
	@include md {
		padding: 24px 12px; }
	@include sm {
		padding: 21px 12px; }
	&__img {
		position: relative;
		margin: 0 auto 16px;
		@include md {
			margin: 0 auto 20px; }
		&-in {
			width: 110%;
			transform: translate3d(-5%,0,0); }
		svg {
			@include lg {
				width: 173px;
				height: 173px; } } }
	&__title {
		margin-bottom: 13px;
		@include f(16, 19, null, 700);
		@include lg {
			margin-bottom: 14px; } }
	&__description {
		margin-bottom: 14px;
		color: $lightGray;
		@include f(16, 24);
		@include lg {
			margin-bottom: 20px;
			@include f(14, 22); } }
	&__social {
		font-size: 0;
		a {
			display: inline-block;
			vertical-align: top;
			padding: 10px;
			color: $white;
			@include hover {
				svg {
					fill: darken(#BEBEBE, 10%); } }
			svg {
				fill: #BEBEBE;
				font-size: 24px;
				transition: fill 0.175s ease-in-out; } } } }
