.roadmap {
	overflow: hidden;
	padding: 135px 0 142px;
	color: $black;
	background: $lightGrayBG;
	@include lg {
		padding: 65px 0 75px; }
	@include md {
		padding: 72px 0 70px; }
	@include sm {
		padding: 17px 0 37px; }
	&__innter {
		padding: 0; }
	&__title {
		margin-bottom: 71px;
		@include md {
			margin-bottom: 48px; }
		@include sm {
			margin-bottom: 33px; } }
	&__items {
		font-size: 0; }
	&__item {
		padding: 23px 15px;
		display: inline-block;
		vertical-align: top;
		width: 33.33%;
		@include lg {
			width: 50%; }
		@include sm {
			padding: 15px 0;
			width: 100%; }
		//DESKTOP
		//start
		&:first-child {
			.roadmap__info:before {
				left: 0;
				width: 5000px; } }
		//start of each row on desktop
		&:nth-child(3n + 4) {
			&:not(:nth-last-child(3)) {
				.roadmap__info:before {
					left: -5000px;
					right: -5000px; } } }
		//end
		&:nth-last-child(2) {
			.roadmap__info:before {
				right: 3px;
				width: 5000px; } }
		&:first-child,
		&:nth-child(3n + 4):not(:nth-last-child(3)),
		&:nth-last-child(2) {
			.roadmap__info:before {
				content: '';
				position: absolute;
				top: 31px;
				border-top: 3px solid $green;
				@include lg {
					display: none; } } }
		//TABLET
		&:first-child,
		&:nth-child(2n + 3):not(:nth-last-child(2)) {
			.roadmap__info:before {
				content: '';
				position: absolute;
				top: 31px;
				border-top: 3px solid $green;
				@include lg {
					display: block; } } }
		&:nth-child(2n + 3) {
			&:not(:last-child) {
				.roadmap__info:before {
					@include lg {
						left: -5000px;
						right: -5000px; } } } }
		//MOBILE
		.roadmap__info:before {
			@include sm {
				content: '';
				position: absolute;
				top: 31px;
				left: -5000px;
				right: -5000px;
				display: block !important;
				border-top: 3px solid $green;
				width: auto; } }
		&:nth-last-child(2) {
			.roadmap__info:before {
				@include sm {
					width: auto; } } }
		//main
		&_main {
			@include lg {
				display: block;
				margin: 0 auto;
				width: 100%;
				max-width: 493px; }
			.roadmap {
				&__info {
					padding-top: 22px;
					@include lg {
						padding-top: 0;
						padding-left: 81px; }
					@include md {
						padding-left: 56px; }
					@include sm {
						padding-left: 50px; }
					&:before {
						@include lg {
							top: 9px !important;
							right: 100%;
							width: 5000px; }
						@include md {
							right: 105%; }
						@include sm {
							right: 100%;
							margin-right: -21px; } } }
				&__subtitle {
					padding: 0 0 5px;
					@include f(18, 24, null, 700);
					@include sm {
						@include f(16, 22); } }
				&__description {
					@include f(16, 22);
					@include sm {
						@include f(14, 20); } }
				&__subtitle,
				&__description {
					letter-spacing: -0.02em; } }
			.icon-arrow-right {
				display: none;
				top: 0 !important;
				right: 100% !important;
				margin-right: -4px;
				@include lg {
					display: block !important; }
				@include md {
					right: 105% !important; }
				@include sm {
					margin-right: -25px;
					right: 100%! important; } } } }
	&__info {
		position: relative;
		@include clr; }
	&__meta {
		margin-bottom: 24px;
		color: $green;
		@include f(14, 17, null, 700); }
	&__subtitle,
	&__description {
		padding: 5px 0;
		letter-spacing: -0.02em; }
	&__subtitle {
		@include f(16, 22);
		span {
			color: $green; } }
	&__description {
		clear: both;
		color: $lightGray;
		@include f(14, 20); }
	.icon-arrow-right {
		position: absolute;
		top: 22px;
		right: 0;
		color: $green;
		font-size: 21px;
		@include lg {
			display: none; } } }
