.box {
	text-align: center;
	&__img {
		margin-bottom: 20px;
		height: 130px;
		@include vertical;
		img {
			display: inline-block;
			vertical-align: bottom; } }
	&__img_minergate {
		width: 106px; }
	&__text {
		margin: 0 auto 36px;
		max-width: 370px;
		color: $lightGray;
		@include f(16, 24);
		@include sm {
			margin-bottom: 11px;
			@include f(14, 22); } }
	&__row {
		margin: 0 auto;
		max-width: 437px;
		@include clr;
		@include md {
			padding: 0 22px; } }
	&__col {
		float: left;
		width: 50%;
		@include sm {
			padding: 15px 0;
			width: 100%; } }
	&__value {
		@include f(24, 24, null, 700);
		@include md {
			margin-bottom: 2px; } }
	&__description {
		color: $lightGray;
		@include f(16, 24); } }
