.team {
	padding: 150px 0 80px;
	color: $black;
	background: $lightGrayBG;
	text-align: center;
	@include lg {
		padding: 60px 0; }
	@include sm {
		padding: 50px 0 20px; }
	&__title {
		margin-bottom: 32px;
		@include lg {
			margin-bottom: 40px; }
		@include sm {
			margin-bottom: 33px; } }
	&__svg {
		position: relative;
		overflow: hidden;
		svg {
			position: absolute;
			top: 0;
			left: 0;
			width: 248px;
			height: 248px; } }
	&__members {
		margin: 0 auto;
		max-width: 1200px;
		@include clr;
		@include md {
			margin: 0 -25px; } }
	&__info {
		margin-top: 95px;
		@include md {
			margin-top: 68px; }
		@include sm {
			margin-top: 45px; } }
	&__member {
		float: left;
		width: 33.33%;
		@include sm {
			float: none;
			margin: 0 auto;
			width: 100%;
			max-width: 290px; } }
	&__row {
		margin: 0 auto;
		max-width: 1087px;
		@include clr;
		@include lg {
			max-width: 487px; }
		@include md {
			max-width: 480px; } }
	&__col {
		float: left;
		width: 50%;
		padding: 0 0 0 55px;
		&:first-child {
			padding: 0 60px 0 0;
			@include md {
				padding: 0; } }
		&:not(:first-child) {
			@include md {
				margin-top: 32px; }
			@include sm {
				margin-top: 28px; } }
		@include lg {
			width: 100%;
			padding: 25px 0; }
		@include md {
			padding: 15px 0; } }
	&__subtitle {
		margin-bottom: 30px;
		@include f(18, 22, null, 700);
		@include lg {
			margin-bottom: 23px; }
		@include md {
			margin-bottom: 38px; }
		@include sm {
			margin-bottom: 26px; } } }
