.select {
	position: relative;
	select {
		display: block;
		padding: 0 40px 0 15px;
		border: 1px solid rgba($black, 0.20);
		border-radius: 3px;
		width: 100%;
		height: 50px;
		color: $black;
		background: $white;
		font-family: 'Karla', Arial, sans-serif;
		font-size: 16px;
		line-height: normal;
		appearance: none;
		&::-ms-expand {
			display: none; } }
	.icon {
		position: absolute;
		top: 50%;
		right: 16px;
		font-size: 12px;
		pointer-events: none;
		transform: translate3d(0,-50%,0); } }
