.tooltipster-follower {
  .tooltipster-box {
    background: #565656;
    border: 2px solid black;
    border-radius: 4px; }
  .tooltipster-content {
    color: white;
    line-height: 18px;
    padding: 6px 14px; }
  &.tooltipster-borderless .tooltipster-box {
    border: none;
    background: #1b1b1b;
    background: rgba(10, 10, 10, 0.9); }
  &.tooltipster-light {
    .tooltipster-box {
      border-radius: 3px;
      border: 1px solid #cccccc;
      background: #ededed; }
    .tooltipster-content {
      color: #666666; } }
  &.tooltipster-noir {
    .tooltipster-box {
      border-radius: 0px;
      border: 3px solid black;
      background: #fff; }
    .tooltipster-content {
      color: black; } }
  &.tooltipster-punk .tooltipster-box {
    border-radius: 5px;
    border: none;
    border-bottom: 3px solid #f71169;
    background: #2a2a2a; }
  &.tooltipster-shadow {
    .tooltipster-box {
      border: none;
      border-radius: 5px;
      background: white;
      box-shadow: 0px 0px 10px 6px rgba(0, 0, 0, 0.1); }
    .tooltipster-content {
      color: #8d8d8d; } } }
